/*
 *  4.0 Navbar
 */

.navbar-custom {
  background-color: #0a99ff;
  border: 0;

  .navbar-brand,
  .navbar-brand:focus,
  .navbar-brand:hover {
    color: #fff;
    font-weight: 600;
  }

  .navbar-nav > li > a {
    color: #FDFDFD;

    &:hover,
    &:focus {
      background-color: transparent;
      color: #fff;
    }
  }

  .nav .open > a,
  .nav .open > a:hover,
  .nav .open > a:focus {
    background-color: transparent;
  }

  .img-profile {
    max-width: 32px !important;
  }
}

/*
 *  4.1 Nav
 */

.task-nav {
  border-radius: $border-radius-sm;
  margin-bottom: .75rem;
}


.task-nav > li.active > a,
.task-nav > li.active > a:hover,
.task-nav > li.active > a:focus {
    color: #0A99FF;
    background-color: transparent;
    border-bottom: 2px solid #0A99FF;
    border-radius: 0;
}